<template><section><h1>Loading </h1>
<hr>
<h3></h3>
<p><code v-pre>type='global'</code> </p>
<div class="demo-block">
   <p-button type="blue" @click="showIt()"></p-button>
   <p-loading type="global" :isShow="showGlobal"></p-loading>
   
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><p-loading type="global" isshow></p-loading>

</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
  <span class="hljs-tag">&lt;<span class="hljs-name">p-loading</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"global"</span> <span class="hljs-attr">isShow</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p-loading</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3></h3>
<p><code v-pre>type='part'</code></p>
<div class="demo-block">
<p-button type="blue" @click="showP()"></p-button>
   <p-loading type="part" :isShow="showPart" showText="..."></p-loading>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><p-loading type="part" isshow showtext="..."></p-loading>

</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
  <span class="hljs-tag">&lt;<span class="hljs-name">p-loading</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"part"</span>  <span class="hljs-attr">isShow</span> <span class="hljs-attr">showText</span>=<span class="hljs-string">"..."</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p-loading</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th></th>
<th></th>
<th></th>
<th></th>
<th></th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td></td>
<td>string</td>
<td>global/part</td>
<td></td>
</tr>
<tr>
<td>isShow</td>
<td></td>
<td>Boolean</td>
<td>true/false</td>
<td>false</td>
</tr>
</tbody>
</table>
</section></template>

<script>
   export default {
	 data () {
		return {
			showGlobal: false,
			showPart: false
		} 
	 },
	 methods: {
		 showIt(e) {
			 let _this = this
			 _this.showGlobal = true
			 setTimeout(function(){
				_this.showGlobal = false
			 }, 3000)
		 },
		  showP() {
		 			 let _this = this
		 			 _this.showPart = true
		 			 setTimeout(function(){
		 				_this.showPart = false
		 			 }, 3000)
		 }
	 }  
   }
   </script>


